.connect__disc {
    background-color: #1875f0;
    border-radius: 50%;
    padding-top: 100%;
    position: relative;
    color: white;

    [class^="icon__"] {
        position: absolute;
        display: flex;
        justify-content: center;
        align-items: center;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        font-size: 8vw;
    }
}



.connect__avatar {
    padding-top: 100%;
    background-color: yellow;
    position: relative;
    box-shadow: 0 2px 6px rgba(0, 0, 0, 0.726);
    // padding: 10px;
    border-radius: 8px;
    transform: rotate(2.5deg);

    &::after {
        content: '';
        border-radius: 6px;
        position: absolute;
        top: 10px;
        bottom: 10px;
        right: 10px;
        left: 10px;
        background-size: cover;
        background-image: url('asset/image/poza.jpg');
    }
}